* {
  margin: 0;
  padding: 0;
}
.TodoBox {
  background-color: aqua;
  display: flex;
  flex-direction: row;
  height: 120vh;
  align-items: center;
  justify-content: space-around;
}
.ListBox {
  /* height: 70vh; */
  width: 25vw;
  border: 3px solid black;
  padding: 8px;
  border-radius: 20px;
  /* background-color: black; */
}
.switch {
  position: relative;
  display: block;
  float: right;
  margin: 20px 50px;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
